import React, { useState, useEffect } from "react";
import "../componets/log-in/login.css";

const CheckEmailPage: React.FC = () => {
  const [ready, setReady] = useState(false);

  useEffect(() => {
    const t = setTimeout(() => setReady(true), 1200); // small delay for effect
    return () => clearTimeout(t);
  }, []);

  return (
    <div
      className="auth-shell"
      style={{
        display: "block",
        width: "100vw",
        height: "100vh",
        position: "relative",
        overflow: "hidden",
      }}
    >
      <aside
        className="brand-side"
        style={{
          width: "100vw",
          height: "100vh",
          position: "relative",
        }}
      >
        <div className="brand-sheen" />
        <div className="brand-pattern" />

        {/* CENTERED CONTENT */}
        <div
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            textAlign: "center",
            padding: "20px",
          }}
        >
          {/* Loader */}
          {!ready && (
            <>
              <div
                className="spinner"
                style={{
                  width: "70px",
                  height: "70px",
                  border: "6px solid rgba(255,255,0,0.25)",
                  borderTopColor: "var(--gold-2)",
                  borderRadius: "50%",
                  animation: "spin 1s linear infinite",
                  margin: "0 auto 20px auto",
                }}
              />

              <p
                style={{
                  color: "var(--off-white)",
                  fontSize: "1.6rem",
                  marginTop: "10px",
                }}
              >
                Đang xử lý…
              </p>
            </>
          )}

          {/* Final message */}
          {ready && (
            <>
              <h1
                style={{
                  color: "var(--off-white)",
                  fontSize: "2.8rem",
                  fontWeight: 700,
                  marginBottom: "10px",
                }}
              >
                Kiểm tra email của bạn
              </h1>

              <p
                style={{
                  color: "var(--light-gray)",
                  fontSize: "1.4rem",
                  maxWidth: "480px",
                  margin: "0 auto",
                  lineHeight: "1.6",
                }}
              >
                Chúng tôi đã gửi liên kết xác minh vào hộp thư của bạn.
                <br />
                Vui lòng mở email và nhấn vào nút xác thực để hoàn tất đăng ký.
              </p>
            </>
          )}
        </div>
      </aside>

      {/* Kill right form-side */}
      <style>
        {`
          @keyframes spin {
            from { transform: rotate(0deg); }
            to { transform: rotate(360deg); }
          }

          .auth-shell .form-side {
            display: none !important;
          }
        `}
      </style>
    </div>
  );
};

export default CheckEmailPage;

export const Head = () => <title>Check Email</title>;
